body {
  height: 100%;
  margin: 0;
  padding: 0 24px;
  font-family: Jost-Regular, Verdana, Tahoma, arial, sans-serif;
}

@font-face {
  font-family: Jost-Light;
  src: local(Jost-Light), url("Jost-Light.765df94a.woff") format("woff"), url("Jost-Light.ff4a845f.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Jost-Regular;
  src: local(Jost-Regular), url("Jost-Regular.017ac04e.woff") format("woff"), url("Jost-Regular.3a0704b5.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Jost-Bold;
  src: local(Jost-Bold), url("Jost-Bold.a93f1d2b.woff") format("woff"), url("Jost-Bold.7ced2bd6.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: ShantellSans-Regular;
  src: local(ShantellSans-Regular), url("ShantellSans-Regular.09ca4f80.woff") format("woff"), url("ShantellSans-Regular.e07428ff.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Noto-Regular;
  src: local(Noto-Regular), url("NotoSerifDisplay-Regular.f5df15fb.woff") format("woff"), url("NotoSerifDisplay-Regular.d3c8db66.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Noto-Bold;
  src: local(Noto-Bold), url("NotoSerifDisplay-Bold.0282eaba.woff") format("woff"), url("NotoSerifDisplay-Bold.f585e46e.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

:root {
  --Jost-Light: "Jost-Light";
  --Jost-Bold: "Jost-Bold";
  --ShantellSans-Regular: "ShantellSans-Regular";
  --Noto-Regular: "Noto-Regular";
  --Noto-Bold: "Noto-Bold";
  --Lemon_tuesday: "Lemon_tuesday";
  --black: #000;
  --white: #fff;
  --download-background-color: #ff4545;
  --yellow: #f7f742;
  --background-blue: #13bcfa;
  --background-blue-facebook: #00759f;
  --shadow: 0px 0px 8px 1px #0000008f;
  --github-background: #524954;
  --cubik-transition: cubic-bezier(.28, .12, 0, .98);
  --cubik-transition-media: cubic-bezier(.4, 0, .2, 1);
  --nav-gradient: linear-gradient(45deg, #07d888, #e40851, #2f00ff, #00ff37, #ec0808, #2600ff, #0bd157, #2f00ff);
  --translate-50-50: translate(-50%, -50%);
}

html {
  scroll-behavior: smooth;
  height: 100%;
}

button {
  cursor: pointer;
  border: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, p {
  margin: 0;
  padding: 0;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.my_name {
  margin-right: 10px;
  font-size: 30px;
}

.nav_list {
  display: flex;
}

.header_container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header_resume {
  display: flex;
}

.btn-download {
  width: 112px;
  justify-content: center;
  display: flex;
}

.download-resume {
  width: 40px;
  height: 40px;
  background-color: var(--black);
  border: 2px solid var(--download-background-color);
  cursor: pointer;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition-duration: .3s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 20px #0000002a;
}

.download-resume:hover {
  width: 110px;
  background-color: var(--download-background-color);
  border-radius: 34px;
  align-items: center;
  transition-duration: .3s;
}

.download-resume:hover .cloud-download {
  width: 50px;
  transition-duration: .3s;
  transform: translateY(150%);
}

.download-resume:before {
  content: "Download Resume PDF";
  color: var(--white);
  font-size: 2px;
  transition-duration: .3s;
  position: absolute;
  top: -28px;
}

.download-resume:hover:before {
  opacity: 1;
  font-size: 13px;
  transition-duration: .3s;
  transform: translateY(30px);
}

.cloud-download {
  width: 16px;
  transition-duration: .3s;
}

.nav_item {
  background: none;
  padding: 6px 16px;
  font-family: inherit;
  font-size: 18px;
  transition: color .3s ease-in-out;
  position: relative;
}

.nav_item:hover {
  color: var(--yellow);
}

.nav_item:before {
  content: "";
  background: var(--nav-gradient);
  z-index: -1;
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  filter: blur(14px);
  opacity: 0;
  background-size: 600%;
  border-radius: 20px;
  transition: opacity .3s ease-in-out;
  animation: 20s linear infinite glowing345;
  position: absolute;
  top: -2px;
  left: -2px;
}

.nav_item:hover:before {
  opacity: 1;
}

@keyframes glowing345 {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 400% 0;
  }

  100% {
    background-position: 0 0;
  }
}

.hero_container {
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 6fr 1fr;
  margin-top: 40px;
  display: grid;
}

.hero_title {
  text-align: center;
}

.hero_quotes {
  font-family: var(--Jost-Light);
  text-transform: uppercase;
  font-size: 28px;
}

.social {
  align-items: center;
  display: flex;
  position: relative;
}

.contacts {
  justify-content: flex-end;
  align-items: center;
  display: flex;
  position: relative;
}

.social_btn, .contacts_btn {
  width: 80px;
  height: 80px;
  box-shadow: var(--shadow);
  z-index: 3;
  border-radius: 50%;
}

.social_hamburger .social_check, .social_hamburger .contacts_check {
  display: none;
}

.social_hamburger svg {
  transition: transform .6s var(--cubik-transition-media);
}

.social_line {
  fill: none;
  stroke: var(--black);
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-width: 3px;
  transition: stroke-dasharray .6s var(--cubik-transition-media), stroke-dashoffset .6s var(--cubik-transition-media);
}

.line-top-bottom {
  stroke-dasharray: 12 63;
}

.social_hamburger .social_check:checked + svg, .social_hamburger .contacts_check:checked + svg {
  transform: rotate(-45deg);
}

.social_hamburger .social_check:checked + svg .line-top-bottom, .social_hamburger .contacts_check:checked + svg .line-top-bottom {
  stroke-dasharray: 20 300;
  stroke-dashoffset: -32.42px;
}

.social_media_btn, .contacts_media_btn {
  transform: var(--translate-50-50);
  width: 40px;
  height: 40px;
  transition: box-shadow .25s var(--cubik-transition-media);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 22%;
}

.social_media_btn:hover, .contacts_media_btn:hover {
  box-shadow: var(--shadow);
}

.contacts_media_btn {
  left: 72%;
}

.linkedIn {
  background-color: var(--background-blue);
}

.linkedIn_icon {
  fill: var(--white);
  transform: scale(1.3);
}

.facebook {
  background-color: var(--background-blue-facebook);
}

.facebook_icon {
  fill: var(--white);
  transform: scale(1.3);
}

.github {
  background-color: var(--github-background);
}

.github_icon {
  fill: #e57fff;
  transform: scale(1.6);
}

.telegram {
  background-color: var(--background-blue);
}

.telegram_icon {
  transform: scale(1.2);
}

.watsapp {
  background-color: #4db32f;
}

.watsapp_icon {
  fill: var(--white);
  transform: scale(1.3);
}

.email {
  background-color: var(--github-background);
}

.email_icon {
  fill: var(--white);
  transform: scale(1.3);
}

.phone {
  background-color: var(--github-background);
}

.phone_icon {
  fill: var(--white);
  transform: scale(1.3);
}

